import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreators } from './store';
import { Form, Input, Button } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import './style.css';

class Login extends Component {
    render() {
        // 已登录
        if (this.props.loginStatus === true) {
            return(
                <Redirect to="/dashboard" />
            );
        }

        // 登录
        const signIn = this.props.signIn;
        const validateMessage = this.props.validateMessage;

        return (
            <div className="login-container">
                <div className="login-header">
                    <div className="logo">运营宝典</div>
                </div>
                <div className="login-container-wrapper">
                    <h1>登录</h1>
                    <Form
                        name="basic"
                        initialValues={{ remember: true }}
                        onFinish={signIn}
                        onFinishFailed={validateMessage}
                    >
                        <Form.Item
                            name="email"
                            rules={[{ required: true, message: '请输入登录邮箱!' }]}
                        >
                            <Input
                                size="large"
                                prefix={<UserOutlined className="site-form-item-icon" />}
                                placeholder="请输入登录邮箱"
                            />
                        </Form.Item>
                        <Form.Item
                            name="password"
                            rules={[{ required: true, message: '请输入登录密码!' }]}
                        >
                            <Input.Password
                                size="large"
                                prefix={<LockOutlined className="site-form-item-icon" />}
                                placeholder="请输入登录密码"
                            />
                        </Form.Item>
                        <Form.Item>
                            <Button
                                type="primary"
                                htmlType="submit"
                                className="login-form-button"
                                size="large"
                            >
                                登录
                            </Button>
                        </Form.Item>
                    </Form>
                    <div className="login-forgot-password">
                        <Link to='/forgot/password'>忘记密码</Link>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loginStatus: state.getIn(['login', 'loginStatus'])
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        signIn(data) {
            dispatch(actionCreators.signIn(data));
        },
        validateMessage(error) {
            console.log('Failed:', error);
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
